<template>
  <div class="container-game2title">
    <div class="line-long" :style="{transform:`scaleX(${linestyle})`}"></div>
    <div class="line-short" :style="{transform:`scaleX(${linestyle})`}"></div>
    <div class="text">
      <div class="text1"><em :style="{transform:`translateY(${emstyle}%)`}">{{title1}}</em></div>
      <div class="text2"><em :style="{transform:`translateY(${emstyle}%)`}">{{title2}}</em></div>
    </div>
  </div>
</template>

<script>
export default {
data() {
  return {
    emstyle:100,
    linestyle:0
  }
},
props:{
  title1:{
    type:String,
    default:"游戏"
  },
  title2:{
    type:String,
    default:"目标"
  }
},
mounted() {
  this.emstyle = 0;
  this.linestyle = 1;
},
}
</script>

<style scoped lang="less">
@media(min-device-width:1025px){
  .container-game2title{
    width: 100%;
    padding-top: 0.5rem;
    position: relative;
    .line-short{
      width: 1rem;
      height: 0.08rem;
      position: absolute;
      left: 0;
      top: 0;
      background: white;
      transform: scaleX(0);
      transform-origin: 0 0;
      transition: transform 0.3s linear;
      &::after{
        content: "";
        display: block;
        border-top: 0.08rem solid transparent;
        border-left: 0.08rem solid white;
        position: absolute;
        top: 0;
        right: -0.08rem;
      }
    }
    .line-long{
      width: 3.5rem;
      height: 0.08rem;
      position: absolute;
      left: 0;
      top: 0.08rem;
      background: white;
      transform: scaleX(0);
      transform-origin: 0 0;
      transition: transform 0.3s linear;
    }
    .text{
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
      .text1{
        width: 100%;
        height: auto;
        overflow: hidden;
      }
      .text2{
       width: 100%;
       height: auto;
       overflow: hidden;
      }
      em{
        font-size: 1.3rem;
        font-weight: bold;
        color: white;
        transform: translateY(100%);
        transition: transform 0.3s linear;
        display: block;
      }
    }
  }
}
@media(max-device-width:1024px){
  .container-game2title{
    width: 60%;
    padding-left: 0.7rem;
    position: relative;
    .line-short{
      width: 0.1rem;
      height: 2.4rem;
      position: absolute;
      left: 0;
      top: 0;
      background: white;
      transform: scaleX(0);
      transition: transform 0.3s linear;
      &::after{
        content: "";
        display: block;
        border-top: 0.1rem solid white;
        border-right: 0.1rem solid transparent;
        position: absolute;
        bottom: -0.1rem;
        left: 0;
      }
    }
    .line-long{
      display: none;
    }
    .text{
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
      .text1{
        width: 100%;
        height: auto;
        overflow: hidden;
      }
      .text2{
       width: 100%;
       height: auto;
       overflow: hidden;
      }
      em{
        font-size: 1rem;
        font-weight: bold;
        color: white;
        transform: translateY(100%);
        transition: transform 0.3s linear;
        display: block;
      }
    }
  }
}
</style>