<template>
  <div class="container-game2" :style="{opacity:isImgLoading}">
    <div class="container-game2-bottom">
      <img :src="'https://lihaibo-li.github.io/lolp/dist/'+require('@/assets/game/game2/1.png')" alt="" class="bottom-img" />
    </div>
    <div class="container-game2-wrapper">
      <GameTitle></GameTitle>
    </div>
    <div class="game2-swiper">
      <div class="container-game2-dian">
        <div class="container-game2-dian-content" :class="{'active-dian':4 == indexAll || 8 == indexAll || 0 ==indexAll}"></div>
        <div class="container-game2-dian-content" :class="{'active-dian':1 == indexAll || 5 == indexAll || 9 == indexAll}"></div>
        <div class="container-game2-dian-content" :class="{'active-dian':2 == indexAll || 6 == indexAll || 10 == indexAll}"></div>
        <div class="container-game2-dian-content" :class="{'active-dian':3 == indexAll || 7 == indexAll || 11 ==indexAll}"></div>
      </div>
      <div class="game2-swiper-show" ref="game2SwiperShow">
        <div class="game2-left" @click="changeToright"></div>
        <div class="game2-right" @click="changeToleft"></div>
        <svg xmls="http://www.w3.org/2000/svg" viewBox="0 0 333 505">
          <path d="M249.7 4l4-4H333v4z" fill="#fff"></path>
          <path d="M83.4 501l-4.1 4H.1v-4z" fill="#fff"></path>
          <path
            d="M1.2 4.1v141.7l31 31-31 31.2v293l314.6-.1 16.2-15.7V207.6l-30.3-30.8 30.3-29.9V4.1z"
            fill="none"
            stroke="#fff"
            stroke-width="2"
            stroke-miterlimit="10"
          ></path>
        </svg>
      </div>
      <ul
        class="game2-swiper-wrapper"
        :style="{
          transform: `translate3d(${Tleft}rem,0,0)`,
          'transition-duration': `${slppe}ms`,
        }"
        ref="game2SwiperWrapper"
        @mousedown="changeLeft"
        @touchstart="changeYD"
      >
        <li
          class="game2-swiper-wrapper-content"
          v-for="(ele, n) in some"
          :key="n"
          :class="{'swiper-active':n == indexAll}"
          ref="game2SwiperContnet"
          :style="{width:`${yidongWidth}px`}"
        >
          <img :src="ele.imgurl" alt="" draggable="false"  :style="{height:`${yidongWidth}px`,width:`${yidongWidth}px`}"/>
          <div class="swiper-text">
            <div class="swiper-text-title" :style="{width:`${yidongWidth}px`}">
              {{ ele.id }}
              <span></span>
              {{ ele.title }}
            </div>
            <div class="swiper-text-content">{{ ele.text }}</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import GameTitle from "@/components/Gameintroduction/Game2-Title";
import imgUrl1 from "@/assets/game/game2/1.jpg";
import imgUrl2 from "@/assets/game/game2/2.jpg";
import imgUrl3 from "@/assets/game/game2/3.jpg";
import imgUrl4 from "@/assets/game/game2/4.jpg";
import forImage from "@/mixins/forImage.js"
export default {
  mixins:[forImage([require('@/assets/game/game2/1.png')])],
  data() {
    return {
      some: [
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl1}`,
          id: 1,
          title: "选择你的路线",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl2}`,
          id: 2,
          title: "游击打野",
          text: "除了专注对线，你也可以进入野区狩猎。讨伐纳什男爵或峡谷巨龙等史诗级野怪，来帮队伍获得强大的增益效果。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl3}`,
          id: 3,
          title: "摧毁防御塔来进攻主堡",
          text: "通往敌方基地的每条路线都有防御塔在把守。摧毁这些建筑，铺出通往胜利的道路。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl4}`,
          id: 4,
          title: "摧毁敌方基地",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl1}`,
          id: 1,
          title: "选择你的路线",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl2}`,
          id: 2,
          title: "游击打野",
          text: "除了专注对线，你也可以进入野区狩猎。讨伐纳什男爵或峡谷巨龙等史诗级野怪，来帮队伍获得强大的增益效果。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl3}`,
          id: 3,
          title: "摧毁防御塔来进攻主堡",
          text: "通往敌方基地的每条路线都有防御塔在把守。摧毁这些建筑，铺出通往胜利的道路。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl4}`,
          id: 4,
          title: "摧毁敌方基地",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl1}`,
          id: 1,
          title: "选择你的路线",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl2}`,
          id: 2,
          title: "游击打野",
          text: "除了专注对线，你也可以进入野区狩猎。讨伐纳什男爵或峡谷巨龙等史诗级野怪，来帮队伍获得强大的增益效果。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl3}`,
          id: 3,
          title: "摧毁防御塔来进攻主堡",
          text: "通往敌方基地的每条路线都有防御塔在把守。摧毁这些建筑，铺出通往胜利的道路。",
        },
        {
          imgurl: `${'https://lihaibo-li.github.io/lolp/dist/'+imgUrl4}`,
          id: 4,
          title: "摧毁敌方基地",
          text: "双方队伍基地的中心就是主堡。保护己方主堡，先一步摧毁敌方的主堡来赢得对战。",
        },
      ],
      yidongWidth:0,
      svgWidth:0,
      svgHeight:0,
      swiperTextHeight:0,
      Tleft: 0,
      slppe: 0,
      leftTimer: null,
      rightTimer: null,
      MoveTimer: null,
      isKey: true,
      indexAll:4,
      fontSlef:0
    };
  },
  components: {
    GameTitle,
  },
  methods: {
    changeToleft() {
      if (this.isKey) {
        if(this.indexAll ==8){
          this.slppe = 0;
          this.indexAll =4;
          this.Tleft = -this.yidongWidth/this.fontSlef/2 - this.yidongWidth/this.fontSlef*4;
          this.$refs.game2SwiperWrapper.style.transform = `translate3d(${this.Tleft}rem,0,0)`
         this.changeToleft();
        }
       else{
         this.isKey = false;
        this.slppe = 300;
        let Tleft1 = this.Tleft
        this.Tleft = Tleft1 - this.$refs.game2SwiperContnet[4].getBoundingClientRect().width/this.fontSlef;
        this.indexAll +=1;
        clearTimeout(this.leftTimer);
        this.leftTimer = setTimeout(() => {
        this.slppe = 0;
        this.isKey = true;
        }, 300);
       }
       
      } else {
        return;
      }
    },
    changeToright() {
      if (this.isKey) {
        if(this.indexAll ==3){
          this.slppe =0;
          this.indexAll =7;
          this.Tleft = -this.yidongWidth/this.fontSlef/2 - this.yidongWidth/this.fontSlef*7;
          this.$refs.game2SwiperWrapper.style.transform = `translate3d(${this.Tleft}rem,0,0)`
        this.changeToright();
        }
       else{
        this.isKey = false;
        this.slppe = 300;
        let Tleft2 = this.Tleft
        this.Tleft = Tleft2 + this.$refs.game2SwiperContnet[4].getBoundingClientRect().width/this.fontSlef;
        this.indexAll -=1;
        clearTimeout(this.rightTimer);
        this.rightTimer = setTimeout(() => {
          this.slppe = 0;
          this.isKey = true;
        }, 300);
       }
      } else {
        return;
      }
    },
    changeLeft(e) {
      let disX = e.clientX;
      let that = this;
      var olderLeft =this.Tleft;
      let contentWidth =
        this.yidongWidth/this.fontSlef;
      let firstTime = 0;
      let lastTime = 0;
      let newL = 0;
      firstTime = new Date().getTime();
      document.onmousemove = function (e) {
        if(that.indexAll <= 3){
          that.indexAll +=4;
          let Tleft1 = -contentWidth/2 - contentWidth*that.indexAll;
          olderLeft = Tleft1
          that.$refs.game2SwiperWrapper.style.transform = `translate3d(${Tleft1}rem,0,0)`;
        }else if(that.indexAll >= 8){
          that.indexAll -=4;
          let Tleft2 = -contentWidth/2 - contentWidth*that.indexAll;
          olderLeft = Tleft2
          that.$refs.game2SwiperWrapper.style.transform = `translate3d(${Tleft2}rem,0,0)`;
        }else{
          newL = (e.clientX - disX) / that.fontSlef;
          that.Tleft = newL + olderLeft;
        }
      };
      document.onmouseup = function () {
        document.onmouseup = null;
        document.onmousemove = null;
        lastTime = new Date().getTime();
        if (lastTime - firstTime <= 300 && Math.abs(newL) < 0.5) {
          if (newL < 0) {
            that.Tleft = olderLeft - contentWidth;
            that.indexAll +=1;
          } else if (newL > 0) {
            that.Tleft = olderLeft + contentWidth;
            that.indexAll -=1;
          } else {
            that.Tleft = olderLeft;
          }
        } else {
          that.Tleft = olderLeft + Math.round(newL/contentWidth) *contentWidth;
          that.indexAll = that.indexAll - Math.round(newL/contentWidth);
        }
       //添加激活样式
         

        that.slppe = 300;
        clearTimeout(that.MoveTimer);
        that.MoveTimer=setTimeout(() => {
          that.slppe = 0;
        }, 300);
      };
    },
    changeYD(e){
       let disX = e.touches[0].pageX;
      let that = this;
      var olderLeft =this.Tleft;
      let contentWidth =
        this.yidongWidth/this.fontSlef;
      let firstTime = 0;
      let lastTime = 0;
      let newL = 0;
      firstTime = new Date().getTime();
      document.ontouchmove = function (e) {
        if(that.indexAll <= 3){
          that.indexAll +=4;
          let Tleft1 = -contentWidth/2 - contentWidth*that.indexAll;
          olderLeft = Tleft1
          that.$refs.game2SwiperWrapper.style.transform = `translate3d(${Tleft1}rem,0,0)`;
        }else if(that.indexAll >= 8){
          that.indexAll -=4;
          let Tleft2 = -contentWidth/2 - contentWidth*that.indexAll;
          olderLeft = Tleft2
          that.$refs.game2SwiperWrapper.style.transform = `translate3d(${Tleft2}rem,0,0)`;
        }else{
          newL = (e.touches[0].pageX - disX) / that.fontSlef;
          that.Tleft = newL + olderLeft;
        }
      };
      document.ontouchend = function () {
        document.ontouchend = null;
        document.ontouchmove = null;
        lastTime = new Date().getTime();
        if (lastTime - firstTime <= 300 && Math.abs(newL) < 0.5) {
          if (newL < 0) {
            that.Tleft = olderLeft - contentWidth;
            that.indexAll +=1;
          } else if (newL > 0) {
            that.Tleft = olderLeft + contentWidth;
            that.indexAll -=1;
          } else {
            that.Tleft = olderLeft;
          }
        } else {
          that.Tleft = olderLeft + Math.round(newL/contentWidth) *contentWidth;
          that.indexAll = that.indexAll - Math.round(newL/contentWidth);
        }
       //添加激活样式
         

        that.slppe = 300;
        clearTimeout(that.MoveTimer);
        that.MoveTimer=setTimeout(() => {
          that.slppe = 0;
        }, 300);
      };
    },
    getFontS(arr){
      let firstNum,lastNum;
      arr.split("").forEach((ele,index)=>{
        if(ele == " "){
          firstNum = index +1;
        }else if(ele == "p" ){
          lastNum = index;
        }
      })
    this.fontSlef = +arr.split("").slice(firstNum,lastNum).join("");
    }
  },
  mounted() {
    this.getFontS(document.documentElement.style.cssText)
    if(document.documentElement.clientWidth <= 1243){
      this.yidongWidth = document.documentElement.clientWidth;
      this.Tleft = -this.yidongWidth/2/this.fontSlef - this.yidongWidth*4/this.fontSlef;
    }
    else{
      this.svgWidth = '8.14rem';
      this.svgHeight = '11.48rem';
      this.yidongWidth = `${9.62*this.fontSlef}`;
      this.Tleft = -this.yidongWidth/this.fontSlef/2 - this.yidongWidth*4/this.fontSlef;
    }
    
  },
};
</script>

<style scoped lang="less">
@media (min-device-width: 1025px) {
  .container-game2 {
    width: 100%;
    height: 17rem;
