      transform: translateY(-1.7rem);
      cursor: grab;
      // background: yellowgreen;
      z-index: 1;
      .game2-swiper-show {
        width: 9.62rem;
        padding: 0.74rem;
        position: absolute;
        left: 50%;
        top: 0.5rem;
        transform: translateX(-50%);
        box-sizing: border-box;
        z-index: 2;
        pointer-events: none;
        svg {
          width: 8.14rem;
          height: 11.48rem;
        }
        .game2-left {
          width: 1.5rem;
          height: 1rem;
          position: absolute;
          left: 0;
          top: 4.25rem;
          pointer-events: auto;
          // background: red;
          &::before {
            content: "";
            display: block;
            width: 100%;
            height: 0.06rem;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background: white;
          }
          &::after {
            content: "";
            display: block;
            width: 0.5rem;
            height: 0.5rem;
            border-top: 0.06rem solid white;
            border-left: 0.06rem solid white;
            position: absolute;
            left: 0.1rem;
            top: 50%;
            transform: translateY(-50%) rotate(-45deg);
          }
        }
        .game2-right {
          width: 1.5rem;
          height: 1rem;
          position: absolute;
          right: 0;
          top: 4.25rem;
          pointer-events: auto;
          &::before {
            content: "";
            display: block;
            width: 100%;
            height: 0.06rem;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background: white;
          }
          &::after {
            content: "";
            display: block;
            width: 0.5rem;
            height: 0.5rem;
            border-top: 0.06rem solid white;
            border-right: 0.06rem solid white;
            position: absolute;
            right: 0.1rem;
            top: 50%;
            transform: translateY(-50%) rotate(45deg);
          }
        }
      }
      .game2-swiper-wrapper {
        height: 100%;
        width: auto;
        display: flex;
        flex-direction: row;
        z-index: 1;
        position: absolute;
        top: 0;
        left:50%;
        // box-sizing: content-box;
        transition: transform 0ms;
        .game2-swiper-wrapper-content {
          height: 100%;
          width: 9.62rem;
          flex: none;
          opacity: 0.6;
          transition: opacity 0.3s linear;
          img {
            width: 100%;
            height: 9.62rem;
          }
          .swiper-text {
            width: 100%;
            height: 3.34rem;
            padding: 0 1.5rem;
            padding-top: 0.3rem;
            box-sizing: border-box;
            .swiper-text-title {
              width: 100%;
              font-size: 0.5rem;
              font-weight: bold;
              color: white;
              // position: relative;
              display: flex;
              flex-direction: row;
              span {
                display: block;
                width: 0.5rem;
                height: 0.67rem;
                position: relative;
                &::after {
                  content: "";
                  display: block;
                  width: 0.08rem;
                  height: 0.08rem;
                  position: absolute;
                  left: 50%;
                  top: 50%;
                  transform: translate(-50%, -50%) rotate(45deg);
                  background: white;
                }
              }
            }
            .swiper-text-content {
              width: 100%;
              font-size: 0.3rem;
              font-weight: bold;
              color: white;
              margin-top: 0.3rem;
            }
          }
        }
        .swiper-active {
          opacity: 1;
        }
      }
      .container-game2-dian{
        width: 9.62rem;
        height: 0.5rem;
        position: absolute;
        left: 50%;
        bottom: -1rem;
        transform: translateX(-50%);
        // background: black;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        .container-game2-dian-content{
          width: 0.2rem;
          height: 0.2rem;
          margin-right: 0.4rem;
          transform: rotate(45deg);
          background: rgba(255, 255, 255, 0.4);
        }
        .active-dian{
          background: white;
        }
      }
    }
  }
}
