<template>
  <div class="container-game4" :style="{opacity:isImgLoading}">
    <div class="container-game4-top">
      <img :src="'https://lihaibo-li.github.io/lolp/dist/'+require('@/assets/game/game4/1.png')" alt="">
    </div>
    <div class="container-game4-content1">
     <div class="content1-top">
       <div class="content1-top-title">
         <Title title1="操控方式" title2="与技能" background1="white" background2="white" :height2="5" :isWhite="true" :isTextWhite="true" :isthemeWhite="true"></Title>
       <div class="content1-top-title-text">每个英雄都拥有独特的技能组，随着对战进展可以逐一升级，其中包括威力惊人的大招。以阿狸为例，她会先使用野性的魔法短暂魅惑敌人，接着便将他们击溃。</div>
       </div>
       <div class="content1-top-skill">
         <ul class="skill-img">
           <li class="skill-img-self" v-for="(ele,n) in some" :key="n" @click="changeSkill(n)" :class="{'self-active':n == SkillActive}"><img :src="ele.img" alt=""></li>
         </ul>
         <div class="skill-text" v-for="(ele,n) in some" :key="n" :class="{'skill-text-active': n == SkillActive}">
           <div class="text1">{{ele.text1}}</div>
           <div class="text2">{{ele.text2}}</div>
         </div>
       </div>
       <div class="content1-top-video">
         <video :src="some[SkillActive].videoUrl" loop="loop" preload="auto" autoplay="autoplay" muted="true"></video>
         <div class="texiao-top">
           <div class="texiao-top-dian"></div>
         </div>
         <div class="texiao-bottom">
           <div class="texiao-bottom-dian"></div>
         </div>
       </div>
     </div>
    </div>
    <div class="container-game4-content2">
      <div class="content2-bottom">
        <div class="content2-bottom-title">
          <GameTitle title1="强化" title2="你的英雄"></GameTitle>
        </div>
        <div class="content2-bottom-strong">
          <ul class="strong-img">
            <li class="strong-img-self" v-for="(ele,n) in some1" :key="n" @click="changeStrong(n)" :class="{'active-img':n == StrongText}"><img :src="ele.url" alt=""></li>
