     videoUrl:"//game.gtimg.cn/images/lgamem/v2/overview/skill/skill-3.mp4"
      },
        {
     img:`${Skill4}`,
     text1:"技能3：魅惑妖术",
     text2:"献出红唇热吻，对命中的第一个敌人造成魔法伤害并施加魅惑，使其走向阿狸。阿狸的技能会对被魅惑妖术命中的敌人造成额外伤害。",
     videoUrl:"//game.gtimg.cn/images/lgamem/v2/overview/skill/skill-4.mp4"
      },
        {
     img:`${Skill5}`,
     text1:"大招: 灵魄突袭",
     text2:"阿狸向前冲锋并发射元气弹，对周围敌人造成魔法伤害。此技能在进入冷却阶段前最多可施放3次。",
     videoUrl:"//game.gtimg.cn/images/lgamem/v2/overview/skill/skill-5.mp4"
      },
      ],
    some1:[
