        url:`${Strong1}`,
        title:"经验值",
        text:"击倒敌方英雄，击杀敌方小兵或野怪，以及摧毁防御建筑物都可获得经验值。获得足够经验值后英雄便会升级，其基础能力值会提升并解锁更多强大技能。",
